.index {
  background-image: url('https://www.dexigner.com/images/news/xxw/29381.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 47.12em;
}

.bienvenida img {
  margin-top: 40px;
}

.btn-login {
  margin-top: 430px;
}

.btn-login .col-xs-6 {
  padding-left: 0;
  padding-right: 0;
}

.btn-login a {
  padding: 15px 40px;
  font-size: 22px;
  color: #fff;
  font-weight: 600;
}

.login{
  background-color: transparent;
  border-color: #fff;
}

.init{
  background:-webkit-gradient(linear,left top, right top,from(#8D24BB) , to(#F81B89));
  background:-webkit-linear-gradient(left,#8D24BB , #F81B89);
  background:-o-linear-gradient(left,#8D24BB , #F81B89);
  background:linear-gradient(to right,#8D24BB , #F81B89);
  border: none;
}

.start{
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
}
.logo{
  padding-top: 10%;
}
.borde{
  border : 5px solid red;
}
.div-btn-signup{
  position: absolute;
  bottom: .5vh;
}
.btn-lyft{
  background-color: #D90472;
  color:white;
  border-radius: none;
}
.navbar-brand{
  margin:0 auto;
}
nav{
  border-bottom: 1px solid #999;
}
.form{
  padding-top: 20px;
  padding-bottom: 20px;
}
.info{
  margin:20px 0 20px 0;
}
.dropdown-menu{
  min-width: 30px;
}
.dropdown-item{
  padding:.25rem 1.2rem;
}
.dropdown-toggle{
  background-color: white;
  border:1px solid #ccc;
}
.inputs{
  margin:5px 0;
}
.inp-l{
  border-left: 0;
}

.inp-r{
  border-right: 0;
}
.input-group-addon {
  background-color: white;
  border-right: 0;
}
.map{
  width: 100%;
  height: 100vh;
}
.value{
  width: 50px;
  border-right: 0;
  font-weight: bold;
}
.sobre{
  z-index: 100;
}
.wrapper {
  position: relative;
}
.over_map {
  position: absolute;
  top: 20px;
  z-index: 99;
}
.logo-gift{
  border-radius: 50%;
  margin-top:-5px;
  padding-top: 2%;
  width: 9vh;
  height: 9vh;
  background-color: #fff;
  border:3px solid #ddd;
}
.pie{
  padding-top:40vh;
}
.div-input-address{
  margin-top: 30vh;
}
.start{
  color:blue;
}
.end{
  color:#D90472;
}
.flex{
  width: 100%;
  height: 70vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: end;
      align-items: flex-end;
}
.address,.price{
  border-bottom:1px solid #ccc;
  width: 100vw;
  height: 10vh;
}
.price{
  background-color: #eee;
  height: 10vh;
}
.address{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background-color: white;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-top: 2.5vh;

}
.car{
  float:left;
}
.labocode{
  -ms-flex-pack: center;
      justify-content: center;
  margin:0 auto;
  padding-top: 50vw;

}

/*Estilos de Sig-up*/

.ocultar{
    background: grey;
}

.sign-up {
	background: yellow
}

.phone .dropdown{
	margin: 0 auto;
	width: 80%;
}
.dropdown img{
	width: 50%;
	display: inline-block;
}
.previus{
	position: absolute;
	top: 2%;
	left: 5%;
}

/* Kely*/
.phone .dropdown{
	margin: 0 auto;
	width: 80%;
}
.dropdown img{
	width: 50%;
	display: inline-block;
}
.phone .row div{
	padding: 0;
}

.phone input{
	width: 90%;
	border:none;
	outline: none;
}
.phone{
	border: 1px solid silver;
	border-radius: 5px;
	padding: 10px 10px 0px 10px;
	margin:10px 0;
}


